.pieces {
    position: absolute;
    left: calc(0.25*var(--tile-size));
    right: 0;
    top: 0;
    bottom: calc(0.25*var(--tile-size));
}

.piece {
    width: 12.5%;
    height: 12.5%;
    position: absolute;
    background-size: 100%;
}

